import "./App.css";
import Home from "./pages/home/index";
import { Switch, BrowserRouter as Router, Route } from "react-router-dom";
import TermsAndConditions from "./pages/terms";
import DropCreation from "./pages/create_drop";
import Signup from "./pages/signup";
import Signup2 from "./pages/signup/index2";
import Login from "./pages/login";
import Profile from "./pages/profile";
import SquareGallery from "./pages/galleries/square-gallery";
import MagGallery from "./pages/galleries/mag-gallery";
import WalletLinks from "./pages/wallet/wallet-links";
import PersonalLinks from "./pages/wallet/personal-links";
import NftDisplay from "./pages/wallet/nft-display";
import ConnectedWallets from "./pages/wallet/connected-wallets";
// import TinderCards from './pages/react-tinder-card/DemoSwiper';
import { useState, useEffect } from "react";
import { FirebaseAuthProvider } from "../src/contexts/FirebaseAuthContext";
// import firebase from "firebase/app";

function App() {
  const [userDetails, setUserDetails] = useState({});

  useEffect(() => {
    let user = {
      name: "Crypto Art Man",
      handle: "cryptoartman",
      bio: "The Drop From Space is a piece that signifies the launch of this incredible app  Drop Magnet! Designed by the lead designer of Drop Magnet, itll be available for auction on Crypto Art Mans OpenSea page from this Friday onwards.",
      image:
        "https://pbs.twimg.com/profile_images/1378299017747165187/oKvJA363_400x400.jpg",
    };
    console.log("user in app", user);
    setUserDetails(user);
  }, []);

  return (
    <Router>
      <FirebaseAuthProvider>
        <Switch>
          <Route
            exact
            path="/"
            render={(props) => (
              <Home {...props} userDetails={userDetails} userLoggedIn={true} />
            )}
          />
          <Route
            path="/terms"
            render={(props) => <TermsAndConditions {...props} />}
          />
          <Route
            path="/square_gallery"
            render={(props) => (
              <SquareGallery
                {...props}
                userDetails={userDetails}
                userLoggedIn={true}
              />
            )}
          />
          <Route
            path="/mag_gallery"
            render={(props) => (
              <MagGallery
                {...props}
                userDetails={userDetails}
                userLoggedIn={true}
              />
            )}
          />
          <Route
            path="/create_drop"
            render={(props) => (
              <DropCreation {...props} userHandle={userDetails.handle} />
            )}
          />
          <Route path="/signup" render={(props) => <Signup {...props} />} />
          <Route path="/signup2" render={(props) => <Signup2 {...props} />} />
          <Route path="/login" render={(props) => <Login {...props} />} />
          <Route
            path="/profile"
            render={(props) => (
              <Profile
                {...props}
                userDetails={userDetails}
                userLoggedIn={true}
              />
            )}
          />
          <Route
            path="/wallet_links"
            render={(props) => (
              <WalletLinks
                {...props}
                userDetails={userDetails}
                userLoggedIn={true}
              />
            )}
          />
          <Route
            path="/nfts"
            render={(props) => (
              <NftDisplay
                {...props}
                userDetails={userDetails}
                userLoggedIn={true}
              />
            )}
          />
          <Route
            path="/cw"
            render={(props) => (
              <ConnectedWallets
                {...props}
                userDetails={userDetails}
                userLoggedIn={true}
              />
            )}
          />
          <Route
            path="/personal_links"
            render={(props) => (
              <PersonalLinks
                {...props}
                userDetails={userDetails}
                userLoggedIn={true}
              />
            )}
          />
          {/* <Route path="/tinder_cards" render={(props) => <TinderCards {...props} userDetails={userDetails} userLoggedIn={true} />} /> */}
        </Switch>
      </FirebaseAuthProvider>
    </Router>
  );
}

export default App;
